<template>
  <TextInput
    type="tel"
    :maxlength="18"
    :multiline="false"
    :show-counter="false"
    @keyup.prevent="onKeyUp"
    class="text-white"
  />
</template>

<script>
import TextInput from "@/components/ui/TextInput";
import { maskPhoneNumber } from "@/utils/FormatUtils";

export default {
  name: "PhoneInput",
  components: { TextInput },
  methods: {
    onKeyUp(event) {
      event.target.value = maskPhoneNumber(event.target.value);
    },
  },
};
</script>
