import { DateTime } from "luxon";

export const SHORT_DATE_FORMAT = "dd.MM.yyyy";

export function normalizeDateTime(date) {
  if (date instanceof Date) {
    return DateTime.fromJSDate(date).setLocale("ru");
  }

  if (typeof date === "string") {
    return DateTime.fromISO(date).setLocale("ru");
  }

  return date.setLocale("ru");
}

export function dateToFormat({ date, format = SHORT_DATE_FORMAT }) {
  if (!date) {
    return "";
  }

  const dateTime = normalizeDateTime(date);

  return dateTime.toFormat(format);
}
