<template>
  <FormsContainer>
    <TypographyWithInfo
      font-weight="heavy"
      font-style="header-1"
      class="mb-5"
      popover-position="bottom-right"
      >{{ localize.tabSourceTitle.text }}
    </TypographyWithInfo>

    <TypographyWithInfo font-weight="heavy" class="mb-5"
      >{{ localize.tabSourceFormDownloadFiles.text }}
    </TypographyWithInfo>

    <div class="d-flex flex-column mb-5" v-if="files.length > 0">
      <TechnologyFileItem
        :file="file"
        :key="file.name"
        :disabled="!editable"
        v-for="(file, index) in files"
        @delete="onDeleteClick(index)"
        class="source-code-form__file-item"
      />
    </div>

    <VTypography class="mb-1">{{
      localize.commonFormDownloadFiles.text
    }}</VTypography>

    <FilePicker
      :file-formats="['doc', 'docx', 'pdf']"
      file-limits="5 мб"
      :loading="loading"
      :disabled="!editable"
      @change="onFilesSelected"
      :title="localize.tabSourceFormDownloadFilesFootnote.text"
    />
  </FormsContainer>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from "vuex";

import FilePicker from "@/components/ui/FilePicker";
import VTypography from "@/components/ui/Typography";
import FormsContainer from "@/components/forms/FormsContainer";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import TechnologyFileItem from "@/components/technology/TechnologyFileItem";
import { FormsTabKeys } from "@/constants/FormConstants";

export default {
  name: "SourceCodeForm",
  components: {
    FilePicker,
    VTypography,
    FormsContainer,
    TechnologyFileItem,
    TypographyWithInfo,
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", ["canEditForm"]),
    ...mapGetters("technology", {
      files: "getFiles",
    }),
    editable() {
      return this.canEditForm(FormsTabKeys.SourceCode);
    },
  },
  data() {
    return {
      loading: false,
    };
  },
  methods: {
    ...mapMutations("technology", ["removeFile"]),
    ...mapActions("technology", ["uploadFiles"]),
    onDeleteClick(index) {
      this.removeFile(index);
    },
    onFilesSelected(files) {
      this.loading = true;

      this.uploadFiles(files).finally(() => {
        this.loading = false;
      });
    },
  },
};
</script>

<style scoped lang="scss">
.source-code-form__file-item {
  &:not(:last-child) {
    margin-bottom: 10px;
  }
}
</style>
