<template>
  <div class="d-flex flex-column" :class="rootClass">
    <div
      class="d-flex inn-input__container"
      :class="{ 'inn-input__container--error': !!error }"
    >
      <div :key="index" v-for="(item, index) in value" class="d-flex">
        <input
          :value="item"
          :maxlength="1"
          ref="innInput"
          placeholder="0"
          v-bind="$attrs"
          @focus="onFocus($event, index)"
          @keydown="onKeyDown($event, index)"
          @change="item = $event.target.value"
          class="text-center align-self-center inn-input__field"
        />

        <div v-if="index < 11" class="align-self-end inn-input__divider" />
      </div>
    </div>

    <VTypography v-if="!!error" class="mt-1 inn-input__error">{{
      error
    }}</VTypography>
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";

const initial = "";

export default {
  name: "InnInput",
  components: { VTypography },
  inheritAttrs: false,
  props: {
    error: String,
    rootClass: String,
    modelValue: String,
  },
  data() {
    const value = {};

    for (let i = 0; i < 12; i++) {
      value[i] = initial[i] || "";
    }

    return {
      value,
    };
  },
  // watch: {
  //   modelValue(value) {
  //     console.log("======", value);
  //   },
  // },
  methods: {
    onFocus(event, index) {
      const currentInput = this.$refs.innInput[index];
      const prevInput = index > 0 ? this.$refs.innInput[+index - 1] : undefined;
      const nextInput =
        index > 0
          ? this.$refs.innInput[this.$refs.innInput.length - 1]
          : undefined;

      if (
        this.$refs.innInput &&
        currentInput?.value === "" &&
        prevInput?.value === "" &&
        nextInput?.value === ""
      ) {
        const firstEmptyInput = this.$refs.innInput.find((x) => x.value === "");

        if (firstEmptyInput) {
          firstEmptyInput.focus();
        }
      } else if (this.$refs.innInput) {
        this.$refs.innInput.forEach((input, idx) => {
          if (idx >= +index) {
            input.value = "";
          }
        });
      }
    },
    onKeyDown(event, index) {
      event.preventDefault();

      const isBackSpace = event.key === "Backspace";
      const isValidDigits = index < 12 && event.code.indexOf("Digit") === 0;

      if (isBackSpace) {
        const currentInput = this.$refs.innInput[index];
        const prevInput =
          index > 0 ? this.$refs.innInput[+index - 1] : undefined;

        if (currentInput) {
          currentInput.value = "";
        }

        if (prevInput) {
          prevInput.value = "";
          prevInput.focus();
        }
      } else if (isValidDigits) {
        const currentInput = this.$refs.innInput[index];
        const nextInput = this.$refs.innInput[+index + 1];

        if (currentInput && !currentInput.value) {
          currentInput.value = event.key;
        }

        if (nextInput) {
          nextInput.focus();
        }
      }

      // if (isBackSpace || isValidDigits) {
      //   const value = this.$refs.innInput.map((x) => x.value).join("");
      //
      //   this.$emit("update:modelValue", value);
      // }
    },
  },
};
</script>

