<template>
  <FormsContainer class="technology-form">
    <TypographyWithInfo
      font-weight="heavy"
      font-style="header-1"
      class="mb-5"
      popover-position="bottom-right"
      >{{ localize.tabDescTechnologyTitle.text }}
      <template v-slot:popover>
        <PopoverContent :width="926">
          <VTypography font-weight="medium"
            >{{ localize.tabDescTechnologyTitle.prompt }}
          </VTypography>
        </PopoverContent>
      </template></TypographyWithInfo
    >
    <TypographyWithInfo font-weight="heavy" class="mb-5"
      >{{ localize.tabDescTechnologyFormDownloadFiles.text }}
    </TypographyWithInfo>

    <div class="d-flex flex-column mb-5" v-if="files.length > 0">
      <TechnologyFileItem
        :file="file"
        :key="file.name"
        :disabled="!editable"
        v-for="(file, index) in files"
        @delete="onDeleteClick(index)"
        class="technology-form__file-item"
      />
    </div>

    <VTypography class="mb-1">{{
      localize.commonFormDownloadFiles.text
    }}</VTypography>

    <FilePicker
      :file-formats="[
        'doc',
        'docx',
        'pdf',
        'xls',
        'xlsx',
        'zip',
        'rar',
        'jpg',
        'png',
      ]"
      file-limits="100 мб"
      :loading="loading"
      :disabled="!editable"
      @change="onFilesSelected"
      :title="localize.tabDescTechnologyFormDownloadFilesFootnote.text"
    />
  </FormsContainer>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from "vuex";

import FilePicker from "@/components/ui/FilePicker";
import VTypography from "@/components/ui/Typography";
import FormsContainer from "@/components/forms/FormsContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import TechnologyFileItem from "@/components/technology/TechnologyFileItem";
import { FormsTabKeys } from "@/constants/FormConstants";

export default {
  name: "TechnologyForm",
  components: {
    FilePicker,
    VTypography,
    PopoverContent,
    FormsContainer,
    TechnologyFileItem,
    TypographyWithInfo,
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", ["canEditForm"]),
    ...mapGetters("technology", {
      files: "getFiles",
    }),
    editable() {
      return this.canEditForm(FormsTabKeys.Technology);
    },
  },
  data() {
    return {
      loading: false,
    };
  },
  methods: {
    ...mapMutations("technology", ["removeFile"]),
    ...mapActions("technology", ["uploadFiles"]),
    onDeleteClick(index) {
      this.removeFile(index);
    },
    onFilesSelected(files) {
      this.loading = true;

      this.uploadFiles(files).finally(() => {
        this.loading = false;
      });
    },
  },
};
</script>

<style scoped lang="scss">
.technology-form__file-item {
  &:not(:last-child) {
    margin-bottom: 10px;
  }
}
</style>
