<template>
  <TextInput
    :maxlength="7"
    :multiline="false"
    :show-counter="false"
    @keyup.prevent="onKeyUp"
    input-type="contrast"
    placeholder="000-000"
  >
    <template v-slot:startAdornment>
      <slot name="startAdornment"></slot>
    </template>
  </TextInput>
</template>

<script>
import TextInput from "@/components/ui/TextInput";
import { maskDivisionCode } from "@/utils/FormatUtils";

export default {
  name: "DivisionCodeInput",
  components: { TextInput },
  methods: {
    onKeyUp(event) {
      event.target.value = maskDivisionCode(event.target.value);
    },
  },
};
</script>
