<template>
  <TextInput
    v-bind="$attrs"
    :maxlength="14"
    class="text-white"
    :multiline="false"
    @keydown="onKeyDown"
    :show-counter="false"
    input-type="contrast"
    @keyup.prevent="onKeyUp"
    placeholder="000-000-000 00"
  />
</template>

<script>
import TextInput from "@/components/ui/TextInput";
import { maskSnils } from "@/utils/FormatUtils";

export default {
  name: "SnilsInput",
  components: { TextInput },
  methods: {
    onKeyUp(event) {
      event.target.value = maskSnils(event.target.value);
    },
    onKeyDown(event) {
      if (event.code !== "Backspace" && event.code.indexOf("Digit") !== 0) {
        event.preventDefault();
      }
    },
  },
};
</script>
